:root {
  /* Define color themes */
  --color-main-1: rgb(190, 177, 170);
  --color-main-2: rgb(58, 78, 83);
  --color-secondary-1: rgb(48, 59, 57);
  --color-secondary-2: rgb(98, 86, 83);
}

@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  font-family: "Noto Sans", sans-serif;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  background-color: var(--color-main-1);
}

#root {
  width: 100%;
  height: 100%;
}

*::-webkit-scrollbar {
  display: none;
}

.body {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3 {
  text-transform: capitalize;
}

.section {
  width: 100%;
  min-height: 100vh;
}

.section-container {
  width: 80%;
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem 0 1rem 0;
  overflow: auto;
}

.section-container h1 {
  color: var(--color-secondary-2);
  font-size: 3rem;
  padding: 1rem 0 2rem 0;
  text-align: center;
}

.section-container p {
  color: var(--color-secondary-2);
  padding-bottom: 2rem;
  font-size: 1.2rem;
}
