@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
.app {
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  position: fixed;
  text-transform: capitalize;
  background-color: var(--color-main-2);
}

.header-container {
  display: flex;
  justify-content: flex-end;
}

.main-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 2rem 1rem 1rem;
  color: var(--color-main-1);
}

.main-nav-item {
  padding: 0.5rem;
}

.footer {
  margin-top: 0.5rem;
  background-color: var(--color-secondary-2);
}

.footer-container {
  padding: 0.8rem;
  display: flex;
  justify-content: center;
}

.footer-sub-container {
  padding: 0 2rem;
}

.footer-links {
}

.footer-links > * {
  padding: 0.2rem 0;
}

.landing {
  width: auto;
  height: 100vh;
  background: var(--color-main-1);
  background: url(/static/media/mountains.9330848c.jpg) no-repeat center;
  background-size: auto 100vh;
}

.landing-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-transform: capitalize;
  color: white;
}

.landing-container h1 {
  text-align: center;
  font-size: 5rem;
}

.tagline {
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
}

.tagline > * {
  padding: 0 0.5rem;
}

.landing-container h2 {
}

#intro-section {
}

#me-portrait {
  width: 300px;
  height: auto;
  /* border-radius: 50%; */
  float: left;
  padding: 0 1rem 1rem 1rem;
  margin-right: 0.5rem;
}

:root {
  /* Define color themes */
  --color-main-1: rgb(190, 177, 170);
  --color-main-2: rgb(58, 78, 83);
  --color-secondary-1: rgb(48, 59, 57);
  --color-secondary-2: rgb(98, 86, 83);
}

* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  font-family: "Noto Sans", sans-serif;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  background-color: rgb(190, 177, 170);
  background-color: var(--color-main-1);
}

#root {
  width: 100%;
  height: 100%;
}

*::-webkit-scrollbar {
  display: none;
}

.body {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3 {
  text-transform: capitalize;
}

.section {
  width: 100%;
  min-height: 100vh;
}

.section-container {
  width: 80%;
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem 0 1rem 0;
  overflow: auto;
}

.section-container h1 {
  color: rgb(98, 86, 83);
  color: var(--color-secondary-2);
  font-size: 3rem;
  padding: 1rem 0 2rem 0;
  text-align: center;
}

.section-container p {
  color: rgb(98, 86, 83);
  color: var(--color-secondary-2);
  padding-bottom: 2rem;
  font-size: 1.2rem;
}

